import { createStore } from "vuex";
import axios from 'axios';

const axiosOptions = { 
    withCredentials: false, 
    timeout: 1000
}

const modules = {}

const state = {
    store: null,
    categories: null,
    items: null
}

const getters =  {
    fullStore: state => ({categories: state.categories, items: state.items, store: state.store})
}

const mutations = {
    setStore(state, data){
        if (data.categories) state.categories = data.categories
        if (data.items) state.items = data.items
        if (data.store) state.store = data.store
    }
}

const actions = {

    async getStoreById({ commit, state }, storeId) {
      return axios.get(`https://hackemy.github.io/livemenu.pages/${storeId}.json`, axiosOptions)
      .then(resp => {
          if (resp.data) {
              commit('setStore', {
                categories: resp.data.categories, 
                items: resp.data.items, 
                store: resp.data.store
              })
            }
      })
      .catch((err) => {
          if (err.response && err.response.status === 400) {
              return
          }
          throw err // to ui catch
      });
  }

}

export default {
    namespaced: false,
    state,
    getters,
    actions,
    mutations
  };

  