<script setup>
defineProps({
  categories: Object,
  currentActive: String,
  mobile: Boolean,
});

const emit = defineEmits(["categoryClicked"]);

const scrollToCategory = (id) => {
  emit("categoryClicked");
  const y =
    document.querySelector("#" + id).getBoundingClientRect().top +
    window.scrollY;
  window.scroll({
    top: y - 60,
    behavior: "smooth",
  });
};
</script>

<template>
  <div
    class="categories d-flex d-md-block"
    :class="{ 'mobile-categories': mobile }"
  >
    <h3 class="d-md-block d-none">Categories</h3>
    <div
      @click="scrollToCategory(`cat-` + id)"
      v-for="(item, id) in categories"
      :key="item"
      class="single-category"
      :class="{ active: `cat-` + id === currentActive }"
    >
      {{ item.names["en"] }}
    </div>
  </div>
</template>

