<script setup>
import { ref, computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useRoute } from "vue-router"
import router from "@/router";

import Banner from "@/components/Banner.vue";
import Categories from "@/components/Categories.vue";
import Items from "@/components/Items.vue";
import HomeSkelton from "@/components/skelton/Home.vue";

const store = useStore();
const route = useRoute();

const storeInfo = ref(null);
const currentActive = ref(null);
const categoryClickedScroll = ref(false);
const lasterror = ref(null);
const dataLoading = ref(null);

await store.dispatch("getStoreById", route.params.storeId).then(() => {
    storeInfo.value = store.getters.fullStore;
    lasterror.value = null
})
.catch((err)=> {
    storeInfo.value = null
    lasterror.value = err

    router.push({ name: "Errors", params: {code: "404"}})
})
.finally(() => {
    dataLoading.value = false
})

const groupedItems = computed(() => {
  const items = {};
  if (!storeInfo.value.items) return [];
  Object.values(storeInfo.value.items).forEach((item) => {
    if (!items[item.cat]) {
      items[item.cat] = [];
    }
    items[item.cat].push(item);
  });
  return items;
});

onMounted(() => {
  if (!currentActive.value)
    currentActive.value = "cat-" + Object.keys(storeInfo.value.categories)[0];

  window.onscroll = () => {
    var currentTemp = -1;
    document.querySelectorAll(".item-category").forEach((section) => {
      const sectionTop = section.offsetTop - 70;

      if (pageYOffset >= sectionTop) {
        if (currentTemp < +section.getAttribute("id").split("-")[1]) {
          currentActive.value = section.getAttribute("id");
        }
      }
    });

    const mobileCategories = document.querySelector(".mobile-categories");

    if (mobileCategories && !categoryClickedScroll.value) {
      const activeCategory = document.querySelector(
        ".mobile-categories .single-category.active"
      );
      const activeReacts = activeCategory.getBoundingClientRect();

      if (screen.width < activeReacts.x + activeReacts.width + 40) {
        mobileCategories.scrollLeft =
          mobileCategories.scrollLeft + activeReacts.x - 12;

      }
    }
  };
});

const categoryClicked = () => {
  categoryClickedScroll.value = true;
  setTimeout(() => {
    categoryClickedScroll.value = false;
  }, 1000);
};
</script>

<template>
  <div class="home">
    <div class="container pt-4">
      <div class="row g-0">
        <div class="col-lg-8 col-12 offset-lg-2">
          <Banner
            :name="storeInfo.store.name"
            :logo_url="storeInfo.store.logo_url"
            :hero_url="storeInfo.store.hero_url"
            :city="storeInfo.store.city"
          />

          <div class="row g-0 mt-sm-4">
            <div class="col-lg-3 col-md-3 col-12">
              <Categories
                class="d-none d-md-block"
                :mobile="false"
                :current-active="currentActive"
                :categories="storeInfo.categories"
              />
            </div>
            <div class="col-lg-9 col-md-9 col-12 no-gutters-mobile">
              <Categories
                class="d-md-none"
                :mobile="true"
                :current-active="currentActive"
                :categories="storeInfo.categories"
                @categoryClicked="categoryClicked"
              />
              <Items :categories="storeInfo.categories" :items="groupedItems" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
.home {
  background-color: rgb(247, 247, 247);
  min-height: 100vh;
}

@media (max-width: 768px) {
  .no-gutters-mobile {
    padding: 0 !important;
  }
}
</style>
